import { createContext, useState } from 'react';

const ConfigContext = createContext();

const ConfigProvider = ({ children }) => {
  
  const [config, setConfig] = useState({
    config: {
        maxVehicles:2,
        capacity:100,
         depotx:35,
         depoty:67,
         horizon:120
    }
});
  const [nodes, setNodes] = useState({
    custNode: [
      {
        id: 0,
        x: 78.000,
        y: 62.000,
        demand: 100,
        begin: 48,
        end: 540,
        service: 90
      },
      {
        id: 1,
        x: 1.000,
        y: 32.000,
        demand: 100,
        begin: 420,
        end: 480,
        service: 90
      },
      {
        id: 2,
        x: 0.000,
        y: 0.000,
        demand: 100,
        begin: 600,
        end: 660,
        service: 90
      },
      {
        id: 3,
        x: 10.000,
        y: 0.000,
        demand: 100,
        begin: 300,
        end: 360,
        service: 90
      },
      {
        id: 4,
        x: 20.000,
        y: 30.000,
        demand: 25,
        begin: 800,
        end: 900,
        service: 90
      },
      {
        id: 5,
        x: 15.000,
        y: 24.000,
        demand: 35,
        begin: 450,
        end: 500,
        service: 90
      }
    ]
  }
  );

  return (
    <ConfigContext.Provider value={{ config, setConfig, nodes, setNodes }}>
      {children}
    </ConfigContext.Provider>
  );
};

export { ConfigProvider, ConfigContext };


